import React from 'react';
import {  Counter, Card, CardGrid, platform, List, IOS, Cell, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

// иконки
import Icon28UserAddOutline from '@vkontakte/icons/dist/28/user_add_outline'; //выздоровело
import Icon28Users from '@vkontakte/icons/dist/28/users'; //заражено
import Icon28User from '@vkontakte/icons/dist/28/user'; //умерло
import Icon28HistoryForwardOutline from '@vkontakte/icons/dist/28/history_forward_outline'; //обновление

//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';

const osname = platform();

const Covid = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24BrowserForward/>}
    </PanelHeaderButton>}
        >
            Коронавирус
        </PanelHeader>
        <Group> 
        <CardGrid>
          <Card size="l" mode="shadow">
          <List>
       <Cell before={<Icon28UserAddOutline />} indicator={<Counter mode="primary">2</Counter>}>Выздоровело:</Cell>
       <Cell before={<Icon28Users />} indicator={<Counter mode="primary">8</Counter>}>Заражено:</Cell>
       <Cell before={<Icon28User />} indicator={<Counter>0</Counter>}>Умерло:</Cell>
       <Cell before={<Icon28HistoryForwardOutline />} indicator={<Counter mode="primary">18:45</Counter>}>Последнее обновление(в часов):</Cell>
     </List>
            <div style={{ height: 10 }} />
          </Card>
        </CardGrid>
    </Group>
    </Panel>
);

export default Covid;